.card {
  background: #fff;
  border-radius: 2px;
  height: 275px;
  margin: 1rem;
  position: relative;
  width: 275px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 275px;
  overflow: hidden;
}

.card > .img-container > img {
  width: 100%;
  height: 100%;
}
